// src/lib/auth-session.js
// 간단한 세션 인코딩/디코딩 유틸리티 (추후 실제 인증 연동 시 교체 용이)

const AUTH_SESSION_COOKIE = "app_session_v1"

/**
 * 세션 쿠키에 저장할 사용자 정보를 문자열로 직렬화합니다.
 * URL-safe 하도록 encodeURIComponent를 활용해 JSON을 감쌉니다.
 */
export function encodeUserSession(user) {
  if (!user || typeof user !== "object") return ""
  try {
    const payload = JSON.stringify({ user })
    return encodeURIComponent(payload)
  } catch {
    return ""
  }
}

/** 쿠키 값을 사용자 객체로 역직렬화합니다. 실패 시 null 반환 */
export function decodeUserSession(value) {
  if (!value || typeof value !== "string") return null
  try {
    const decoded = decodeURIComponent(value)
    const parsed = JSON.parse(decoded)
    if (parsed && typeof parsed === "object" && parsed.user && typeof parsed.user === "object") {
      const { name = "", email = "", avatar = "" } = parsed.user
      if (!name && !email) return null
      return { name, email, avatar }
    }
  } catch {
    return null
  }
  return null
}

/** 쿠키 스토어(cookies())에서 현재 사용자 정보 추출 */
export function getSessionUserFromCookies(cookieStore) {
  if (!cookieStore || typeof cookieStore.get !== "function") return null
  const stored = cookieStore.get(AUTH_SESSION_COOKIE)
  if (!stored || !stored.value) return null
  return decodeUserSession(stored.value)
}

/** 세션 쿠키 이름 (middleware 및 라우트에서 공유) */
export const AUTH_COOKIE_NAME = AUTH_SESSION_COOKIE

/**
 * 데모용 OpenID SSO 사용자 정보.
 * 실제 환경에서는 이 부분을 OpenID 프로필 응답으로 교체하면 된다.
 */
export const DUMMY_SSO_USER = Object.freeze({
  name: "Demo SSO User",
  email: "sso.user@example.com",
  avatar: "https://www.gravatar.com/avatar/?d=mp",
})
